import CONF from './config'
import Api from '@/utils/api'

const manageInfoApi = CONF.manageVisit.API

export default {
  /**
   *[根据条件获取房屋信息列表]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getHouseInfo(params = {}) {
    const res = await Api.get(manageInfoApi['getAbnormalList'], params)
    return res
  },
  async getFormInfo(params = {}) {
    const res = await Api.get(manageInfoApi['getFormInfo'], params)
    return res
  },
  async postSaveMessage(params = {}) {
    const res = await Api.post(manageInfoApi['postSaveMessage'], params)
    return res
  },
  async checkResult(params = {}) {
    const res = await Api.post(manageInfoApi['checkResult'], params)
    return res
  },
  async queryAllCommunity(params = {}) {
    const res = await Api.get(manageInfoApi['queryAllCommunity'], params)
    return res
  },

  async getFeedbackResult(params = {}) {
    const res = await Api.get(manageInfoApi['getFeedbackResult'], params)
    return res
  },

  async getCaseBackGround(params = {}) {
    const res = await Api.get(manageInfoApi['getCaseBackGround'], params)
    return res
  }
}
