<template>
  <div class="feedback"  v-loading="feedbackLoading">
    <div style="display: flex; justify-content: space-between;">
      <h3 style="margin-bottom:40px">走访核实单</h3>
      <el-button type="primary" size="small" style="height: 30px;" @click="$router.push({ path: 'manage-visit' })">返回
      </el-button>
    </div>
    <h4 style="border-bottom:1px solid #999;padding-bottom:10px">核实信息</h4>
    <div class="list-box">
      <div class="title">房屋信息:</div>
      <div class="cont">{{infoList.houseInfo ? infoList.houseInfo : ''}}</div>
    </div>
    <div class="list-box">
      <div class="title">房主信息:</div>
      <div class="cont"> {{infoList.houseLandlordInfo ? infoList.houseLandlordInfo : ''}}</div>
    </div>
    <div class="list-box">
      <div class="title">入住人员信息:</div>
      <div class="cont">{{infoList.houseResidentInfo ? infoList.houseResidentInfo : ''}}</div>
    </div>
    <div class="list-box">
      <div class="title">走访原因:</div>
      <div class="cont">{{infoList.visitReason ? infoList.visitReason : ''}}</div>
    </div>
    <div class="list-box" style="padding: 10px 0;">
      <div class="title">核实人员:</div>
      <div class="cont">
        <div class="people-box" style="margin-bottom:10px" v-model="checkPersons[0]">
          <span style="margin-right:10px">姓名：{{checkPersons[0].name}}</span>
          <!--<el-input v-model="officerInfo.officerName" clearable style="width: 120px;" size="small" placeholder="请选择"></el-input>-->
          <span style="margin:0 10px">联系方式：{{checkPersons[0].contactInfo}}</span>
          <!--<el-input v-model="officerInfo.officerPhone" clearable style="width: 180px;" size="small" placeholder="请选择"></el-input>-->
          <!--<el-button size="small" v-if="index === 0" type="primary" @click="onAddCheckPersons" style="margin-left:20px;height:32px;margin-top:4px">新增</el-button>-->
          <!--<el-button size="small" v-if="index !== 0" type="danger" @click="onDelCheckPersons(index)" style="margin-left:20px;height:32px;margin-top:4px">删除</el-button>-->
        </div>
      </div>
    </div>
    <div class="list-box">
      <div class="title">走访人员:</div>
      <div class="cont">
        <div style="display:inline-block;margin:0 10px" v-for="(name, index) in visitPersons" :key="index">
          <el-input :value="name" clearable style="width: 100px;" size="small" placeholder="请输入"
                    @change="onVisitInput(index, $event)"></el-input>
        </div>
        <el-button size="small" type="primary" @click="onAddVisitPersons"
                   style="margin-left:20px;height:32px;margin-top:4px">新增
        </el-button>
        <el-button size="small" type="danger" @click="onDelVisitPersons"
                   style="margin-left:20px;height:32px;margin-top:4px">删除
        </el-button>
      </div>
    </div>
    <div>
      <h4 style="border-bottom:1px solid #999;padding-bottom:10px">核实结果</h4>
      <!--<div style="margin-top:10px">-->
      <!--<add-people-table title="寄住信息" :data="lodgePersonsTable"></add-people-table>-->
      <!--</div>-->
      <div style="margin-top:10px">
        <b style="padding-right: 20px">房屋状态判断</b>
        <el-radio-group v-model="houseStatus">
          <el-radio label="出租" value="出租">出租</el-radio>
          <el-radio label="自住" value="自住">自住</el-radio>
          <el-radio label="空置" value="空置">空置</el-radio>
        </el-radio-group>
      </div>
      <div style="margin-top:10px">
        <add-rent-table title="租客人员信息" :data="rentPersonsTable" houseStatus="1"
                        v-if="houseStatus == '出租'"></add-rent-table>
        <add-rent-table title="自主人员信息" :data="selfPersonsTable" houseStatus="0"
                        v-if="houseStatus == '自住'"></add-rent-table>
      </div>
      <div style="margin:30px 0px">
        <b style="padding-right: 20px">房屋潜在隐患</b>
        <el-radio-group v-model="hiddenDanger.judgeDanger">
          <el-radio label="1" value=1>存在安全隐患</el-radio>
          <el-radio label="0" value=0>不存在安全隐患</el-radio>
        </el-radio-group>
      </div>
      <div v-if="hiddenDanger.judgeDanger == 1">
        <div style="margin:30px 0px">
          <b style="padding-right: 20px">人员隐患</b>
          <el-checkbox-group v-model="peopleDanger">
            <el-checkbox label="1" name="type">有精神障碍患者</el-checkbox>
            <el-checkbox label="2" name="type">有生活失意心态失衡对社会不满人员</el-checkbox>
            <el-checkbox label="3" name="type">有个人极端暴力者</el-checkbox>
            <el-checkbox label="4" name="type">有关注人员</el-checkbox>
            <el-checkbox label="5" name="type">有其它需纳入视线人员（需在备注内详细说明）</el-checkbox>
            <el-input style="display: inline-block;width: 20%;padding-left: 10px"
                      v-model="hiddenDanger.personOtherDanger"></el-input>
          </el-checkbox-group>
        </div>
        <div style="margin:30px 0px">
          <b style="padding-right: 20px">房屋隐患</b>
          <el-checkbox-group v-model="houseDanger">
            <el-checkbox label="1" name="type">存在私自建等违法建筑</el-checkbox>
            <el-checkbox label="2" name="type">多人混居群租房</el-checkbox>
            <el-checkbox label="3" name="type">违规存储危险易爆物品</el-checkbox>
            <el-checkbox label="4" name="type">消防通道不畅</el-checkbox>
            <el-checkbox label="5" name="type">不符安全标准三合一场所</el-checkbox>
            <el-checkbox label="6" name="type">其他隐患</el-checkbox>
            <el-input style="display: inline-block;width: 20%;padding-left: 10px"
                      v-model="hiddenDanger.houseOtherDanger"></el-input>
          </el-checkbox-group>
        </div>
      </div>
      <!--<div style="margin-top:15px">-->
      <!--<b>其他原因:</b>-->
      <!--<el-input-->
      <!--type="textarea"-->
      <!--style="margin-top:20px"-->
      <!--:autosize="{ minRows: 2, maxRows: 4}"-->
      <!--placeholder="请输入内容"-->
      <!--v-model="reasionVal"-->
      <!--&gt;-->
      <!--</el-input>-->
      <!--</div>-->
    </div>
    <div style="text-align: center; padding-bottom: 100px;margin-top:30px">
      <el-button @click="onConfirm">确认</el-button>
      <el-button type="primary" plain @click="save(0)">保存</el-button>
      <el-button @click="reset">重置</el-button>
    </div>
    <v-dialog ref="vDialog"></v-dialog>
  </div>
</template>

<script>
  import VDialog from './dialog'
  import Server from '../server.js'
  import AddRentTable from '../add-rent-table'
  import Cookies from 'js-cookie'

  export default {
    components: {VDialog, AddRentTable},
    props: [],
    data() {
      return {
        tableData: [],
        infoList: {},
        checkPersons: [{name: '', contactInfo: '', key: 1}],
        visitPersons: [''],
        lodgePersonsTable: [],
        rentPersonsTable: [],
        selfPersonsTable: [],
        reasionVal: '',
        houseStatus: '出租',
        hiddenDanger: {
          houseDanger:'',
          personDanger:'',
          judgeDanger: '',
          personDangerOrig: '',
          personOtherDanger: '',
          houseDangerOrig: '',
          houseOtherDanger: ''
        },
        peopleDanger: [], //人员隐患
        houseDanger: [], // 房屋隐患
        officerInfo: {},
        feedbackLoading: false
      }
    },
    methods: {
      async getServer(proCollId) {
        var that = this;
        this.feedbackLoading = true
        await Server.getFormInfo({
          proCollId
        }).then(res => {
          // 获取当前登录信息
          if (Cookies.get('officerInfo')) {
            that.officerInfo = JSON.parse(Cookies.get('officerInfo'))
          }
          that.infoList = res.data || {}
          if (!res.data) return
          that.checkPersons = [{name: that.officerInfo.officerName, contactInfo: that.officerInfo.officerPhone}] //核实人员
          // that.visitPersons = res.data.visitPersons || [''] // 走访人员
          // // this.checkPersons = this.addKey(res.data.checkPersons) || [{name: '', contactInfo: '', key: 1}]
          // // if (!res.data.checkResult) return
          // // this.reasionVal = res.data.checkResult.otherReason || ''

          that.houseStatus = res.data.houseStatus || '' // 房屋状态
          // 房屋隐患
          that.hiddenDanger = res.data.hiddenDanger || {}
          if (res.data.hiddenDanger!= null && res.data.hiddenDanger.personDangerOrig!= null) {
            that.peopleDanger = res.data.hiddenDanger.personDangerOrig.split(',')
          }
          if (res.data.hiddenDanger!= null && res.data.hiddenDanger.houseDangerOrig!= null) {
            that.houseDanger = res.data.hiddenDanger.houseDangerOrig.split(',')
          }

          // 根据房屋状态判断入住人员
          if (that.houseStatus == '自住') {
            that.selfPersonsTable = res.data.checkResult.lodgePersons || []
          }
          if (that.houseStatus == '出租') {
            that.rentPersonsTable = res.data.checkResult.rentPersons || []
          }
          if (res.data.checkResult != null && res.data.checkResult.lodgePersons != null) {
            that.lodgePersonsTable = res.data.checkResult.lodgePersons ? res.data.checkResult.lodgePersons.map(item => {
              return {...item, edit: false}
            }) : [];
          }
          if (res.data.checkResult != null && res.data.checkResult.rentPersons != null) {
            that.rentPersonsTable = res.data.checkResult.rentPersons ? res.data.checkResult.rentPersons.map(item => {
              return {...item, edit: false}
            }) : []
          }
        })
        this.feedbackLoading = false
      },
      onAddCheckPersons() {
        let idx = this.checkPersons[this.checkPersons.length - 1].key
        this.checkPersons.push({name: '', contactInfo: '', key: idx + 1})
      },
      onDelCheckPersons(num) {
        this.checkPersons.splice(num, 1)
      },
      onAddVisitPersons() {
        this.visitPersons.push('')
      },
      onDelVisitPersons() {
        this.visitPersons.pop()
      },
      onVisitInput(index, name) {
        this.visitPersons.splice(index, 1, name)
      },
      addKey(arr) {
        if (!arr) return null
        return arr.map((item, idx) => {
          return {
            ...item,
            key: idx + 'origin'
          }
        })
      },
      reset() {
        this.getServer(this.$route.query.proCollId)
      },
      warning(message) {
        this.$message({
          showClose: true,
          duration: 1000,
          message: message,
          type: 'error'
        });
        return false;
      },
      /**
       * 反馈确认数据验证
       * @returns {*}
       */
      validateEle() {
        // 走访人员
        if (this.visitPersons == "") {
          return this.warning("走访人员不能为空");
        }
        // 房屋状态
        if (this.houseStatus == "") {
          return this.warning("房屋状态不能为空");
        }
        // 入住人员信息
        if (this.houseStatus == "出租") {
          if (this.rentPersonsTable.length == 0) {
            return this.warning("请输入人员信息")
          }
          for (var item in this.rentPersonsTable) {
            if (item.edit == false) {
              return this.warning("人员信息未保存")
            }
          }
        }
        if (this.houseStatus == "自住") {
          if (this.selfPersonsTable.length == 0) {
            return this.warning("请输入人员信息")
          }
          for (var item in this.selfPersonsTable) {
            if (item.edit == true) {
              return this.warning("人员信息未保存")
            }
          }
        }
        // 房屋隐患
        if (this.hiddenDanger.judgeDanger == null || this.hiddenDanger.judgeDanger == "") {
          return this.warning("请选择房屋潜在隐患")
        }
        if (this.hiddenDanger.judgeDanger == 1) {
          if (this.peopleDanger.length <= 0 && this.houseDanger.length <= 0) {
            return this.warning("请选择安全隐患")
          }
          if ((this.peopleDanger.indexOf("5") != -1 && (this.hiddenDanger.personOtherDanger == null || this.hiddenDanger.personOtherDanger == ""))
            || this.peopleDanger.indexOf("5") == -1 && !(this.hiddenDanger.personOtherDanger == null || this.hiddenDanger.personOtherDanger == "")) {
            return this.warning("人员隐患其他，填写错误");
          }
          if ((this.houseDanger.indexOf("6") != -1 && (this.hiddenDanger.houseOtherDanger == null || this.hiddenDanger.houseOtherDanger == ""))
            || this.houseDanger.indexOf("6") == -1 && !(this.hiddenDanger.houseOtherDanger == null || this.hiddenDanger.houseOtherDanger == "")) {
            return this.warning("房屋隐患其他，填写错误");
          }
        }
        return true
      },
      save(auditConfirm) {
        // 走访人员不能为空
        if (this.visitPersons == "") {
          return this.warning("走访人员不能为空");
        }
        // 根据房屋状态判断入住人员信息
        var residentsInfo = []
        if (this.houseStatus == '出租') {
          residentsInfo = this.rentPersonsTable
        } else if (this.houseStatus == '自住') {
          residentsInfo = this.selfPersonsTable
        } else {
          residentsInfo = []
        }
        // 补充房屋隐患
        this.hiddenDanger.personDangerOrig = this.peopleDanger.join(",")
        this.hiddenDanger.houseDangerOrig = this.houseDanger.join(",")

        let params = {
          ...this.infoList,
          "abnormalInfoId": this.$route.query.id,
          "checkPersons": this.checkPersons,
          "houseStatus": this.houseStatus,
          // "checkResult": {
          //   "lodgePersons": this.lodgePersonsTable,
          //   "rentPersons": this.rentPersonsTable,
          //   "otherReason": this.reasionVal
          // },
          "visitPersons": [...this.visitPersons],
          "residentsInfo": residentsInfo,
          auditConfirm: auditConfirm
        }
        console.log(JSON.stringify(params))
        Server.postSaveMessage(params).then(res => {
          if (res.code === 200) {
            this.$router.go(-1)
            this.$message({
              showClose: true,
              duration: 1000,
              message: '保存成功',
              type: 'success'
            });
          } else {
            this.$message({
              showClose: true,
              duration: 1000,
              message: '保存失败',
              type: 'error'
            });
          }
        })
      },
      async onConfirm() {
        if (this.validateEle()) {
          await this.save(1);
        } else {
          return
        }
        const {id} = this.$route.query
        const {proCollId} = this.$route.query
        var houseSpecialMark = ""
        if (this.houseStatus == '自住') {
          this.selfPersonsTable.forEach(function(value,i){
            if (value.caseBackGround != null && value.caseBackGround != "" && value.caseBackGround != undefined) {
              houseSpecialMark += ("," + value.caseBackGround)
            }
          })
        }
        if (this.houseStatus == '出租') {
          this.rentPersonsTable.forEach(function(value,i){
            if (value.caseBackGround != null && value.caseBackGround != "" && value.caseBackGround != undefined) {
              houseSpecialMark += ("," + value.caseBackGround)
            }
          })
        }
        const params = {id : id, houseStatus: this.houseStatus, houseSpecialMark: houseSpecialMark,proCollId: proCollId}
        const res = await Server.checkResult(params)
        if (res && res.data) {
          this.$message({
            showClose: true,
            duration: 1000,
            message: '确认成功',
            type: 'success'
          });
        }
      }
    },
    computed: {},
    mounted() {
      this.getServer(this.$route.query.proCollId)
    }
  }
</script>

<style lang="less" scoped>
  .feedback {

  .list-box {
    display: flex;
    min-height: 40px;
    line-height: 40px;

  .title {
    width: 150px;
