<template>
  <div>
    <div style="margin:20px 0">
      <b>{{title}}</b>
      <el-button size="small" type="primary" @click="addPeople" style="margin: 0 30px;">新增人员</el-button>
    </div>
    <el-table :data="data" border :cell-style="cellStyle">
      <template v-for="(column, index) in columns">
        <el-table-column
          :key="index"
          :prop="column.prop"
          :align="column.align"
          :label="column.label"
          :width="column.width"
        >
          <template slot-scope="scope">
            <template v-if="scope.row.edit">
              <el-select v-if="column.prop === 'relation'" class="edit-input" size="mini"
                         v-model="scope.row[column.prop]">
                <el-option label="常住" value="常住"></el-option>
                <el-option label="暂住" value="暂住"></el-option>
                <el-option label="寄住" value="寄住" v-if="houseStatus == 0"></el-option>
                <el-option label="其他" value="其他" v-if="houseStatus == 1"></el-option>
              </el-select>
              <el-select v-else-if="column.prop === 'sex'" class="edit-input" size="mini"
                         v-model="scope.row[column.prop]">
                <el-option label="男" value="男"></el-option>
                <el-option label="女" value="女"></el-option>
              </el-select>
              <el-date-picker v-else-if="column.prop === 'visitDate'"
                v-model="scope.row[column.prop]"
                type="date"
                placeholder="选择日期" format="yyyy-MM-dd" value-format="yyyy-MM-dd">
              </el-date-picker>
              <el-input v-else-if="column.prop === 'idNumber'" class="edit-input" size="mini"
                         v-model="scope.row[column.prop]" @blur="getCaseBackGround(scope)"></el-input>
              <el-input v-else-if="column.prop === 'caseBackGround'" disabled class="edit-input" size="mini" :autofocus="true"
                        v-model="scope.row[column.prop]"></el-input>
              <el-input v-else class="edit-input" size="mini" :autofocus="true"
                        v-model="scope.row[column.prop]"></el-input>
            </template>
            <template v-else>
              <span>{{ scope.row[column.prop] }}</span>
            </template>
          </template>
        </el-table-column>
      </template>
      <el-table-column label="操作" width="120" align="center">
        <template slot-scope="scope">
          <el-button type="text" @click="onEdit(scope)">
            {{scope.row.edit ? '保存' : '编辑'}}
          </el-button>
          <el-button type="text" @click="onDel(scope)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  import Server from './server.js'
  export default {
    props: {
      data: {
        type: Array
      },
      title: {
        type: String
      },
      houseStatus: {
        type: String
      }
    },
    data() {
      return {
        columns: [
          {prop: 'name', label: '姓名', width: '100', align: 'center'},
          {prop: 'relation', label: '关系', width: '100', align: 'center'},
          {prop: 'sex', label: '性别', width: '100', align: 'center'},
          {prop: 'nation', label: '民族', width: '100', align: 'center'},
          {prop: 'idNumber', label: '公民身份证号码', width: '180', align: 'center'},
          {prop: 'permanentAddress', label: '户籍地址', width: '70', align: 'center'},
          {prop: 'outflow', label: '是否流出', width: '100', align: 'center'},
          {prop: 'outflowInfo', label: '流出原因及详址', width: '', align: 'center'},
          {prop: 'caseBackGround', label: '案底情况', width: '130', align: 'center'},
          {prop: 'workPlace', label: '工作单位/服务处所', width: '130', align: 'center'},
          {prop: 'post', label: '职务', width: '130', align: 'center'},
          {prop: 'phone', label: '联系方式', width: '130', align: 'center'},
          {prop: 'visitDate', label: '时间', width: '130', align: 'center'},
          // {prop: 'checkPerson', label: '核实人员', width: '130', align: 'center'},
          // {prop: 'visitPerson', label: '走访人员', width: '130', align: 'center'},
        ]
      }
    },
    methods: {
      getCaseBackGround({$index}) {
      if (this.data[$index].idNumber == undefined || this.data[$index].idNumber == null || this.data[$index].idNumber == "") {
        return;
      }
      var that = this;
        Server.getCaseBackGround({
          idCardNum: that.data[$index].idNumber
        }).then(res => {
          that.$set(that.data[$index], 'caseBackGround', res.data);
        })
      },
      cellStyle() {
        return {
          padding: '5px 0'
        }
      },
      onEdit({$index}) {
        if (this.data[$index].edit == undefined) {
          this.$set(this.data[$index], 'edit', false);
        }
        // 如果是保存按钮需要做校验
        if (this.data[$index].edit == true) {
          if (!this.validateRisedent(this.data[$index])) {
            this.$message.error('人员信息输入不完整！')
          } else {
            this.data[$index].edit = !this.data[$index].edit
          }
        } else {
          this.data[$index].edit = !this.data[$index].edit
        }
      },
      onDel({$index}) {
        this.data.splice($index, 1)
      },
      validateErrorEle(data) {
        if (data == null || data == "") {
          return true
        } else {
          return false
        }
      },
      validateRisedent(data) {
        if (this.validateErrorEle(data.name) || this.validateErrorEle(data.relation) ||
          this.validateErrorEle(data.sex) || this.validateErrorEle(data.nation) ||
          this.validateErrorEle(data.idNumber) || this.validateErrorEle(data.permanentAddress) ||
          this.validateErrorEle(data.outflow) || this.validateErrorEle(data.outflowInfo) ||
          this.validateErrorEle(data.phone) || this.validateErrorEle(data.visitDate)) {
          return false;
        } else {
          return true;
        }
      },
      addPeople() {
        this.data.push({
          name: '',
          relation: '',
          sex: '',
          nation: '',
          idNumber: '',
          permanentAddress: '',
          outflow: '',
          outflowInfo: '',
          caseBackGround: '',
          phone: '',
          // checkPerson: '',
          // visitPerson: '',
          edit: true
        })
      }
    }
  }
</script>

